/* This stylesheet
    has the common styling for common div elements
    accross multi pages

/* font basis

html
    scroll-behavior: smooth

.crumbs > .crumb-link
    color: $light
    font-weight: 600

header
  position: fixed
  z-index: 100
  width: 100%

.override-title-color
    color: #EFF1F3

.navbar-end > .navbar-item
    font-weight: 600
    font-size: 1.2rem


.foot-links > .foot-link
    color: $light
    font-weight: 800

p, code
    color: $white
    font-size: 1rem

a, span 
    font-size: 1rem !important

svg, img
    margin-top: 0.5rem

.section > figure
    padding: 1rem


#breadcrumbs
    align-items: center
/* purgecss start ignore */
@media screen and (min-width: 1023px)
  .bounceOut 
    -webkit-animation-name: bounceIn
    animation-name: bounceIn
    -webkit-animation-duration: 0.4s
    animation-duration: 0.4s

.shadowed
    -webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75)
    -moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75)
    box-shadow: 5px 3px 5px 0px rgba(0,0,0,0.75)

.is-shadow-top
    -webkit-box-shadow: 1px -5px 11px 0px rgba(0,0,0,0.75)
    -moz-box-shadow: 1px -5px 11px 0px rgba(0,0,0,0.75)
    box-shadow: 1px -5px 11px 0px rgba(0,0,0,0.75)

.has-slight-radius
    border-radius: 0.7rem


.is-sticky
    position: sticky
    top: 5%

.is-overflow
    margin-top: 1.5rem
    max-height: 120vmin
    overflow: scroll
    -ms-overflow-style: none
    scrollbar-width: none
    -webkit-overflow-scrolling: touch
    scroll-behavior: smooth
    &::-webkit-scrollbar
        display: none

.disable-selection
    -moz-user-select: none 
    -ms-user-select: none
    -khtml-user-select: none 
    -webkit-user-select: none
    -webkit-touch-callout: none


/* override bulma implementations
.column.is-one-fifth
    @media screen and (max-width: 1023px)
        width: 0%
        display: none
        visibility: hidden
    @media screen and(max-width: 768px)
        display: block
        visibility: visible
        width: 100%


label
    color: $light !important
    font-size: $size-7 !important
span
    color: $white
    font-size: $size-7
input
    font-size: $size-7
    color: $light !important
a.navbar-item
    color: $white !important
    font-size: $size-7
    &.active
        background-color: $black !important
        color: $light !important
        &:focus, :focus-within
            background-color: $black !important
            color: $light !important
            outline: none
        &:hover
            background-color: $light !important
            color: $info !important
    &:hover
        background-color: $light !important
        color: $info !important

.brand
    color: $white !important
    &:hover
        background-color: $light !important
        color: $info !important


.blink-me
  animation: blinker 1s linear infinite
  background-color: $white
  width: 10px
  height: 2px

@keyframes blinker
  50%
    opacity: 0